import * as actionTypes from './actionsTypes';
import axios from 'axios';

export const authStart = ()=>{      //loading  spinner
    return {
        type:actionTypes.AUTH_STRAT
    };
};

export const authSuccess = (token,userId)=>{
    return{
        type:actionTypes.AUTH_SUCCESS,
        idToken:token,
        userId:userId
    };
};

export const authFail = (error)=>{
    return{
        type: actionTypes.AUTH_FAIL,
        error:error
    };
};

export const logout=()=>{
    //localstorage
    localStorage.removeItem('token');
    localStorage.removeItem('expirationDate');
    localStorage.removeItem('userId');
    return {
        type: actionTypes.AUTH_LOGOUT
    };
};

export const checkAuthTimeout=(expirationTime)=>{
    return dispatch=>{
        setTimeout(() => {
            dispatch(logout());
        }, expirationTime * 1000); //ms  s
    };
};


//auth
export const auth = (email, password, isSignup) =>{   //signupsignin
    return dispatch=>{
        dispatch(authStart());
        const authData= {
            email: email,
            password:password,
            returnSecureToken: true
        };
        let url='https://identitytoolkit.googleapis.com/v1/accounts:signUp?key=AIzaSyAgMGjA4YKyl3D957SXERhcyGEgUbqZUwk';
        if(!isSignup){
            url='https://identitytoolkit.googleapis.com/v1/accounts:signInWithPassword?key=AIzaSyAgMGjA4YKyl3D957SXERhcyGEgUbqZUwk';
        }
        axios.post(url,authData)
                .then(response=>{
                    //localstoragetokentimeout
                    const expirationData = new Date(new Date().getTime() + response.data.expiresIn * 1000);//sms
                    localStorage.setItem('token',response.data.idToken); //token
                    localStorage.setItem('expirationDate',expirationData);
                    localStorage.setItem('userId',response.data.localId);
                    //id
                    dispatch(authSuccess(response.data.idToken,response.data.localId));
                    //
                    dispatch(checkAuthTimeout(response.data.expiresIn));
                })
                .catch(err=>{
                    dispatch(authFail(err.response.data.error));   //axioserr
                });
    };
};


//pathreducerauthstate
export const setAuthRedirectPath = (path)=>{
    return {
        type:actionTypes.SET_AUTH_REDIRECT_PATH,
        path:path
    };
};


export const authCheckState = ()=>{
    return dispatch=>{
        const token = localStorage.getItem('token');
        if(!token){
            dispatch(logout());
        }else{
            const expirationDate = new Date(localStorage.getItem('expirationDate'));
                //lsstringnew date
            if(expirationDate > new Date()){  //
                const userId = localStorage.getItem('userId');
                dispatch(authSuccess(token,userId));
                dispatch(checkAuthTimeout((expirationDate.getTime()-new Date().getTime())/1000));
            }else {      //
                dispatch(logout());
                //
                
            };
        };
    };
};


